/* Onboarding Address - Plain CSS Only */

.onboarding-address-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background-color: #fff;
    padding-bottom: 24px;
}

/* Header */
.onboarding-address-header {
    display: flex;
    align-items: center;
    padding: 16px;
    gap: 16px;
    background-color: #fff;
}

.onboarding-address-title {
    font-size: 20px;
    font-weight: 700;
    color: var(--text-primary, #000);
    margin: 0;
}

/* Form Styles */
.onboarding-address-form {
    display: flex;
    flex-direction: column;
    gap: 16px;
    padding: 0 16px;
    flex: 1;
}

.onboarding-address-input-group {
    position: relative;
    border: 1px solid #E5E7EB;
    border-radius: 24px;
    padding: 8px 16px;
    display: flex;
    align-items: center;
    background-color: #fff;
}

.onboarding-address-input-wrapper {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.onboarding-address-label {
    font-size: 12px;
    color: #9CA3AF;
    margin-bottom: 2px;
    font-weight: 500;
}

.onboarding-address-input {
    border: none;
    outline: none;
    font-size: 16px;
    font-weight: 500;
    color: var(--text-primary, #000);
    width: 100%;
    padding: 0;
    background: transparent;
}

.onboarding-address-input:read-only {
    cursor: default;
    color: #6B7280;
}

/* CTA Button */
.onboarding-address-cta-container {
    padding: 16px;
    margin-top: auto;
}

.onboarding-address-cta-btn {
    width: 100%;
    padding: 16px;
    background-color: #4CAF50;
    color: #fff;
    border: none;
    border-radius: 30px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    text-align: center;
    display: block;
    text-decoration: none;
}

.onboarding-address-cta-btn:active {
    opacity: 0.9;
}

.onboarding-address-cta-btn:disabled {
    background-color: #9CA3AF;
    cursor: not-allowed;
}

/* Dark Theme Support */
.theme-dark .onboarding-address-container,
.theme-dark .onboarding-address-header,
.theme-dark .onboarding-address-input-group {
    background-color: #1E1E1E;
    color: #fff;
}

.theme-dark .onboarding-address-title,
.theme-dark .onboarding-address-input {
    color: #fff;
}

.theme-dark .onboarding-address-input-group {
    border-color: #374151;
}

.theme-dark .onboarding-address-label {
    color: #9CA3AF;
}